.post a,
a.link,
.button {
  color: #FF512F;
  position: relative;
  z-index: 1; }
  .post a strong,
  a.link strong,
  .button strong {
    color: inherit; }
  .post a:hover, .post a:focus,
  a.link:hover,
  a.link:focus,
  .button:hover,
  .button:focus {
    cursor: pointer; }
  .post a:before,
  a.link:before,
  .button:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    z-index: -1;
    background-color: #FF330A;
    visibility: hidden;
    transform: translate(0px, 2px);
    opacity: 0;
    backface-visibility: hidden;
    transition: all .15s ease-in-out; }
  .post a:hover:before, .post a:focus:before,
  a.link:hover:before,
  a.link:focus:before,
  .button:hover:before,
  .button:focus:before {
